import Immutable from 'immutable';

const SET_LOADING = 'loading/set';
export const setLoading = (displayText = 'Loading...') => ({
  type: SET_LOADING,
  payload: {
    displayText,
    isLoading: true,
  },
});

const CLEAR_LOADING = 'loading/clear';
export const clearLoading = () => ({
  type: CLEAR_LOADING,
});

const defaultState = new Immutable.Map({
  isLoading: false,
  displayText: undefined,
});
const reducer = (state = defaultState, action) => {
  switch (action.type) {
    case SET_LOADING:
      return new Immutable.Map(action.payload);
    case CLEAR_LOADING:
      return defaultState;
    default:
      return state;
  }
};

export default reducer;
