import React from 'react';
import PropTypes from 'prop-types';
import { Text, withFormState } from 'informed';

export const requiredValidator = value => value ? undefined : 'Required.';

const TextInput = ({ disabled, field, formState, label, validate }) => {
  const { errors } = formState;
  const errorText = errors[field];
  const hasError = typeof errorText !== 'undefined';

  let cssClass = 'form-control form-control-lg';
  if (hasError) cssClass += ' is-invalid';

  return (
    <div className="form-group">
      <label htmlFor={field}>{label}</label>
      <Text
        className={cssClass}
        autoComplete="off"
        field={field}
        id={field}
        validate={validate}
        disabled={disabled}
        validateOnBlur
      />
      {hasError
        ? <div className="invalid-feedback">{errorText}</div>
        : null
      }
    </div>
  );
};

TextInput.propTypes = {
  disabled: PropTypes.bool,
  field: PropTypes.string.isRequired,
  label: PropTypes.string.isRequired,
  validate: PropTypes.func,
};

TextInput.defaultProps = {
  disabled: false,
};

export default withFormState(TextInput);
