import React from 'react';
import PropTypes from 'prop-types';
import logo from './logo.png';
import './Header.css';

const Header = ({ eventTitle }) => (
  <header>
    <img src={logo} alt="DSA Los Angeles" />
    <h1>{eventTitle}</h1>
    <div className="block-off">&nbsp;</div>
  </header>
);

Header.propTypes = {
  eventTitle: PropTypes.string,
};
Header.defaultProps = {
  eventTitle: 'DSA-LA Event Sign In',
};

export default Header;
