import { connect } from 'react-redux';

import SignInForm from './SignInForm';
import { logSignIn, clearSuccess } from '../../store/signin';

const mapState = (state) => ({
  displaySuccess: state.getIn(['signin', 'displaySuccess'], false),
});
const mapDispatch = ({
  onSubmit: logSignIn,
  clearSuccess,
});

export default connect(mapState, mapDispatch)(SignInForm);
