import React from 'react'
import PropTypes from 'prop-types';
import './Event.css';

export const eventProps = {
  title: PropTypes.string,
  start_date: PropTypes.string,
  onClick: PropTypes.func,
};

const getDisplay = input => {
  const date = new Date(input);
  const tz = { timeZone: 'UTC' };
  return date.toLocaleString('en-US', tz);
  // const datePart = date.toLocaleDateString('en-US');
  // const timePart = date.toLocaleTimeString('en-US', tz);
  // return `${datePart} ${timePart}`;
};

const Event = ({ title, description, start_date, onClick }) => (
  <button className="btn btn-outline-info btn-event" onClick={onClick}>
    <h3>{title}</h3>
    <p dangerouslySetInnerHTML={{ __html: description }} />
    <p>Starts at {getDisplay(start_date)}</p>
  </button>
);

Event.propTypes = eventProps;

export default Event;
