import Immutable from 'immutable';
import * as api from '../api';
import { setLoading, clearLoading } from "./loading";
import { clearError, setError } from './error';

const SET_SUCCESS = 'signin/success/set';
const setSuccess = () => ({
  type: SET_SUCCESS,
})

const CLEAR_SUCCESS = 'signin/success/clear';
export const clearSuccess = () => ({
  type: CLEAR_SUCCESS,
});

export const logSignIn = formValues => (dispatch, getState) => {
  dispatch(clearError());
  dispatch(clearSuccess());
  dispatch(setLoading());
  const eventId = getState().getIn(['setup', 'selectedEvent']);
  const password = getState().get('password');
  const toSubmit = {
    ...formValues,
    eventId,
  };
  return api
    .saveSignIn(toSubmit, password)
    .then(() => {
      dispatch(clearLoading());
      dispatch(setSuccess());
    })
    .catch(() => {
      dispatch(clearLoading());
      dispatch(setError('An error occurred.  Please try again.'));
    });
};

const defaultState = new Immutable.Map({
  displaySuccess: false
});
const reducer = (state = defaultState, action) => {
  switch (action.type) {
    case SET_SUCCESS:
      return state.merge({ displaySuccess: true });
    case CLEAR_SUCCESS:
      return state.merge({ displaySuccess: false });
    default:
      return state;
  }
};

export default reducer;
