import Immutable from 'immutable';

const SET_ERROR = 'error/set';
export const setError = (text) => ({
  type: SET_ERROR,
  payload: { text },
});

const CLEAR_ERROR = 'error/clear';
export const clearError = () => ({
  type: CLEAR_ERROR,
});

const defaultState = new Immutable.Map({
  text: undefined,
});
const reducer = (state = defaultState, action) => {
  switch (action.type) {
    case SET_ERROR:
      return new Immutable.Map(action.payload);
    case CLEAR_ERROR:
      return defaultState;
    default:
      return state;
  }
};

export default reducer;
