import { connect } from 'react-redux';
import Layout from './Layout';

const mapStateToProps = (state) => {
  const selectedEventId = state.getIn(['setup', 'selectedEvent']);
  const eventTitle = state.getIn(['events', selectedEventId, 'title']);
  const errorText = state.getIn(['error', 'text']);
  return { eventTitle, errorText };
}

export default connect(mapStateToProps)(Layout);
