import Immutable from 'immutable';
import * as api from '../api';
import { setError } from './error';
import { setLoading, clearLoading } from './loading';

const mapEvents = (events = []) => events.reduce((acc, curr) =>
  acc.set(curr.id, new Immutable.Map(curr)), new Immutable.OrderedMap());

const SET_EVENTS = 'events/set';
export const loadEvents = () => (dispatch, getState) => {
  dispatch(setLoading());
  const password = getState().get('password');
  return api
    .getEvents(password)
    .then(events => {
      dispatch({
        type: SET_EVENTS,
        payload: { events: mapEvents(events) }
      });
      dispatch(clearLoading());
    })
    .catch(() => {
      dispatch(setError('Error fetching events.  Did you enter a correct password?'));
      dispatch(clearLoading());
    });
};

const defaultState = new Immutable.List();
const reducer = (state = defaultState, action) => {
  switch (action.type) {
    case SET_EVENTS: {
      return action.payload.events;
    }
    default:
      return state;
  }
}

export default reducer;
