import React, { useEffect } from 'react';
import { Form } from 'informed';
import TextInput, { requiredValidator } from '../TextInput';

const Password = ({ onSubmit }) => {
  useEffect(() => {
    const firstInput = document.querySelector('input:not([style="display: none;"]').focus();
    firstInput && firstInput.focus();
  });

  return (
    <div className="container">
      <Form id="password-form" onSubmit={onSubmit}>
        <input autoComplete="false" name="hidden" type="text" style={{ display: 'none' }} />
        <TextInput
          field="password"
          label="Enter your secret password."
          validate={requiredValidator}
        />
        <div className="btn-group">
          <button className="btn btn-primary" type="submit">Submit</button>
        </div>
        <div className="btn-group">
          <button className="btn btn-light" type="reset">Reset</button>
        </div>
      </Form>
    </div>

  );
};

export default Password;
