import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import EventList from '../EventList';
import Loading from '../Loading';
import Password from '../Password';
import SignInForm from '../SignInForm';

const Container = ({ hasEvent, hasPassword, isLoading }) => {
  if (isLoading) return <Loading />;
  if (!hasPassword) return <Password />;
  if (hasEvent) return <SignInForm />
  return <EventList />;
};
Container.propTypes = {
  hasEvent: PropTypes.bool,
  hasPassword: PropTypes.bool,
  isLoading: PropTypes.bool,
};

const mapStateToProps = (state) => {
  const isLoading = state.getIn(['loading', 'isLoading']);

  const event = state.getIn(['setup', 'selectedEvent']);
  const hasEvent = typeof event !== 'undefined';
  const hasPassword = !!state.get('password');

  return { hasEvent, hasPassword, isLoading };
};

export default connect(mapStateToProps)(Container);
