import React from 'react';
import PropTypes from 'prop-types';
import { Form } from 'informed';
import Success from './Success';
import TextInput, { requiredValidator } from '../TextInput';


class SignInForm extends React.Component {
  componentDidMount() {
    const firstInput = document.querySelector('input:not([style="display: none;"]').focus();
    firstInput && firstInput.focus();
  }

  render() {
    const { onSubmit, clearSuccess, displaySuccess } = this.props;
    return (
      <div className="container">
        <Form id="signin-form" onSubmit={onSubmit}>
          {displaySuccess && <Success selfDestruct={clearSuccess} />}
          <input autoComplete="false" name="hidden" type="text" style={{ display: 'none' }} />
          <TextInput
            field="firstName"
            label="First Name"
            validate={requiredValidator}
          />
          <TextInput
            field="lastName"
            label="Last Name"
            validate={requiredValidator}
          />
          <TextInput
            field="emailAddress"
            label="Email Address"
            validate={requiredValidator}
          />
          <TextInput
            field="phoneNumber"
            label="Phone Number"
            validate={requiredValidator}
          />
          <TextInput
            field="zipCode"
            label="Zip Code"
            validate={requiredValidator}
          />
          <div className="btn-group">
            <button className="btn btn-primary" type="submit">Submit</button>
          </div>
          <div className="btn-group">
            <button className="btn btn-light" type="reset">Reset</button>
          </div>
        </Form>
      </div>
    );
  }
}

SignInForm.propTypes = {
  onSubmit: PropTypes.func.isRequired,
  displaySuccess: PropTypes.bool.isRequired,
  clearSuccess: PropTypes.func.isRequired,
};

export default SignInForm;
