import * as mocks from './mocks';

const eventsUrl = process.env.REACT_APP_EVENTS_URL;
const signInsUrl = process.env.REACT_APP_SIGNIN_URL;

// Useful for debugging.
const useMocks = false;
export const getEvents = (password) => {
  if (useMocks) {
    // simulate a network request by taking some time
    return new Promise((resolve) => {
      setTimeout(() => {
        resolve(mocks.events);
      }, 2000);
    });
  }
  return fetch(eventsUrl, {
    headers: {
      'Accept': 'application/json',
      'Authorization': password,
    }}).then(x => x.json());
};

export const saveSignIn = (values, password) => {
  const mapped = {
    "event_id": values.eventId,
    "first_name": values.firstName,
    "last_name": values.lastName,
    "email_address": values.emailAddress,
    "phone_number": values.phoneNumber,
    "zip_code": values.zipCode,
  };
  return fetch(signInsUrl, {
    method: 'POST',
    headers: {
      'Accept': 'application/json',
      'Authorization': password,
    },
    body: JSON.stringify(mapped),
  });
}
